.App {
  text-align: center;
  background-color: #111111;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.App > h1 {
  color: white;
  margin-top: 30px;
  margin-bottom: 10px;
}

.App > .slider > input {
  width: 25%;
  margin: auto;
  margin-top: 20px;
  margin-bottom: 20px;
  appearance: none;
  background-color: #222222;
  color: white;
  border-radius: 10px;
  height: 20px;
}

.App > .slider > p {
  color: white;
  font-size: larger;
}

.entryContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 20px;
}

.entry {
  width: 350px;
  align-items: center;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 10px;
  border-radius: 10px;
  color: white;
  text-decoration: none;
  transition: 200ms;
  cursor: pointer;
}

.entry:hover {
  scale: 1.02;
  transition: 200ms;
}

.entry > button {
  all: unset;
  background-color: #111111;
  padding: 10px;
  font-size: larger;
  border-radius: 10px;
  cursor: pointer;
  width: 100%;
  margin: 10px;
  margin-top: 50px;
}

.entry > img {
  margin: 10px;
}

.entry > h2 {
  font-size: large;
  text-wrap: balance;
  background-color: #111111;
  border-radius: 10px;
  padding: 10px;
  width: 100%;
  margin: 10px;
}

.entry > p {
  margin: 10px;
  width: 66%;
  background-color: #111111;
  padding: 10px;
  border-radius: 10px;
  font-size: 20px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
